export const displayMap = (locations) => {
    mapboxgl.accessToken = 'pk.eyJ1IjoiY2Ftbmd1eWVuIiwiYSI6ImNsOGZ4eDl4MDAxZ3kzcnA0d2x0cnZldWcifQ.mMG906IxryLpAq4_Yn8t4w';

    var map = new mapboxgl.Map({
        container: 'map',
        style: 'mapbox://styles/camnguyen/cl8fy9mjm003e14mstyw5z7hn',
        scrollZoom: false,
        // center: [-118.113491, 34.111745],
        // zoom: 10,
        // interactive: false
    });

    const bounds = new mapboxgl.LngLatBounds();

    locations.forEach((loc) => {
        // Create marker
        const el = document.createElement('div');
        el.className = 'marker';

        // Add marker
        new mapboxgl.Marker({
            element: el,
            anchor: 'bottom',
        })
            .setLngLat(loc.coordinates)
            .addTo(map);

        // Add popup
        new mapboxgl.Popup({
            offset: 30,
        })
            .setLngLat(loc.coordinates)
            .setHTML(`<p>Day ${loc.day}: ${loc.description}</p>`)
            .addTo(map);

        // Extend map bounds to include current location
        bounds.extend(loc.coordinates);
    });

    map.fitBounds(bounds, {
        padding: {
            top: 200,
            bottom: 150,
            left: 100,
            right: 100,
        },
    });
};
